require('normalize.css');
require('styles/App.scss');

import React from 'react'
import ResultComment from '../sources/ResultComment'
import SocialBar from './SocialBar'

let ResultCard = ({score, resetHandler}) => {
	let commentText

	for (let comment of ResultComment) {
	    if ((score >= comment.minScore) && (score <= comment.maxScore)) {
	      commentText = comment.commentText
	    }
	}
	return (
    <div className='surface'>
    	<div className="resultArea">
			<h2><span className="highlight">{score}</span></h2>
			<p>{commentText}</p>
		</div>
		<SocialBar/>
		<button className='btnPlayAgain' onClick={resetHandler}></button>
		<div className="reference"></div>
	</div>
	)
}
export default ResultCard


