require('normalize.css');
require('styles/App.scss');

import React from 'react'

let OptionText = ({options, handleOptionClick}) =>
  <ul className='optionPart'>
    {options.map((option) =>
        <li key={option} onClick={handleOptionClick}><span className='optionBox'></span>{option}</li>
    )}
  </ul>

export default OptionText


