require('normalize.css');
require('styles/App.scss');

import React from 'react'
import QuestionText from './QuestionText'
import OptionText from './OptionText'

let ChoiceCard = ({currentQA, handleOptionClick, questionNumber, totalQuestionNumber}) =>
	<div className={currentQA.layout}>
		<div className='surface'>
			<QuestionText qid={(questionNumber + 1)} question={currentQA.question} />
			<div className="allBar"></div>
			<div className="partBar" style={{width: (questionNumber/totalQuestionNumber).toFixed(2)* 100 + '%'}}></div>
			<OptionText options={currentQA.options} handleOptionClick={handleOptionClick} question={questionNumber} type={currentQA.type} />
		</div>
	</div>
export default ChoiceCard


