require('normalize.css');
require('styles/App.scss');

import React from 'react'
import CoverCard from './CoverCard'
import ChoiceCard from './ChoiceCard'
import ResultCard from './ResultCard'
import QuizData from '../sources/QuizData'
//import memory from './Tracker'

let AppComponent = React.createClass({

	/*componentWillMount () {
	    memory.requestUUID()
	    memory.sendMessage('stationspotting', 'stage', 'start', '')
	    memory.sendMessage('stationspotting', 'userAgent', window.navigator.userAgent, '')
	    memory.sendMessage('stationspotting', 'url', window.location.href, '')
	    memory.sendMessage('stationspotting', 'referrer', window.document.referrer, '')
	  },*/

	getInitialState: () => ({
		stage: 'cover',
		questionNumber: 0,
    	totalScore: 0
	}),

	handleOptionClick (event) {
		let currentNumber = this.state.questionNumber
		let totalQuestionNumber = QuizData.length
		let nextStage = 'qa'
		let optionChosen = event.target.textContent
		let optionScore = 0

		if (currentNumber + 1===totalQuestionNumber){
	      nextStage = 'result'
	    }

	    if (optionChosen === QuizData[currentNumber].correctOption) {
	      optionScore = 1
	    }

		this.setState({
	    	questionNumber: currentNumber + 1,
	    	totalScore: this.state.totalScore + optionScore,
	    	stage: nextStage
	    })
	},

	startClickHandler () {
		this.setState({
			stage: 'qa',
			questionNumber: 0,
			totalScore: 0
		})
	},

	render() {
		let questionNumber = this.state.questionNumber
	    let currentQA = QuizData[questionNumber]
	    let totalQuestionNumber = QuizData.length
		if (this.state.stage === 'cover') {
	  		return <CoverCard startClickHandler = {this.startClickHandler}/>
	    } else if (this.state.stage === 'qa'){
	    	return <ChoiceCard currentQA = {currentQA}
	    					   handleOptionClick = {this.handleOptionClick}
	    					   questionNumber={questionNumber}
	    					   totalQuestionNumber={totalQuestionNumber}/>
		} else {
	  		return <ResultCard score={this.state.totalScore} resetHandler={this.startClickHandler}/>
		}
	}
})

export default AppComponent;



